import React from "react";
import { HorizontalBar, Bar, Doughnut } from "react-chartjs-2";
import {
  AreaChart,
  Area,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
} from "recharts";

export default function Dashboard() {
  /**************Institute chart start***************** */
  const instituteData = (canvas) => {
    const ctx = canvas.getContext("2d");

    /*** Gradient ***/
    const tot_institute_gradient = ctx.createLinearGradient(0, 0, 770, 0);
    tot_institute_gradient.addColorStop(0, "#ff2260");
    tot_institute_gradient.addColorStop(1, "#ff736b");

    const university_gradient = ctx.createLinearGradient(0, 0, 770, 0);
    university_gradient.addColorStop(0, "#553eff");
    university_gradient.addColorStop(1, "#00f0ff");

    const college_gradient = ctx.createLinearGradient(0, 0, 270, 0);
    college_gradient.addColorStop(0, "#0087ff");
    college_gradient.addColorStop(1, "#00e09f");

    const school_gradient = ctx.createLinearGradient(0, 0, 370, 0);
    school_gradient.addColorStop(0, "#423eff");
    school_gradient.addColorStop(1, "#fe3eff");
    /***************/

    return {
      labels: ["Total Institute", "University", "College", "School"],
      datasets: [
        {
          label: "# of Institute",
          data: [100, 60, 20, 20, 0, 100],
          backgroundColor: [
            tot_institute_gradient,
            university_gradient,
            college_gradient,
            school_gradient,
          ],
        },
      ],
    };
  };

  var instituteOptions = {
    scales: {
      yAxes: [
        {
          ticks: {
            beginAtZero: true,
          },
        },
      ],
    },
  };
  /**************Institute chart end***************** */

  /**************Payment chart start***************** */
  const paymentData = (canvas) => {
    const ctx = canvas.getContext("2d");

    /*** Gradient ***/

    const invoice_send_gradient = ctx.createLinearGradient(0, 0, 750, 0);
    invoice_send_gradient.addColorStop(0, "#553eff");
    invoice_send_gradient.addColorStop(1, "#00f0ff");

    const payment_received_gradient = ctx.createLinearGradient(0, 0, 550, 0);
    payment_received_gradient.addColorStop(0, "#ff2260");
    payment_received_gradient.addColorStop(1, "#ff736b");

    const due_gradient = ctx.createLinearGradient(0, 0, 770, 0);
    due_gradient.addColorStop(0, "#423eff");
    due_gradient.addColorStop(1, "#fe3eff");
    /***************/

    return {
      labels: ["Invoice Send", "Payment Received", "Due"],
      datasets: [
        {
          label: "Total",
          data: [100, 60, 40, 0, 100],
          backgroundColor: [
            invoice_send_gradient,
            payment_received_gradient,
            due_gradient,
          ],
        },
      ],
    };
  };

  var paymentOptions = {
    scales: {
      yAxes: [
        {
          ticks: {
            beginAtZero: true,
          },
        },
      ],
    },
  };
  /**************Payment chart end***************** */

  /* /****************bandwidth chart stard**********************/

  const BandWidthData = (canvas) => {
    const ctx = canvas.getContext("2d");

    /*** Gradient ***/

    const invoice_send_gradient = ctx.createLinearGradient(0, 0, 0, 300);
    invoice_send_gradient.addColorStop(0, "#3da9f0");
    invoice_send_gradient.addColorStop(1, "#3da9f0");

    const payment_received_gradient = ctx.createLinearGradient(0, 0, 550, 0);
    payment_received_gradient.addColorStop(0, "#3da9f0");
    payment_received_gradient.addColorStop(1, "#3da9f0");

    const due_gradient = ctx.createLinearGradient(0, 0, 770, 0);
    due_gradient.addColorStop(0, "#3da9f0");
    due_gradient.addColorStop(1, "#3da9f0");

    /***************/

    return {
      labels: ["1", "2", "3", "4", "5", "6"],
      datasets: [
        {
          data: [12, 19, 3, 5, 2, 3],
          backgroundColor: invoice_send_gradient,
        },
        {
          data: [2, 3, 20, 5, 1, 4],
          backgroundColor: payment_received_gradient,
        },
        {
          data: [3, 10, 13, 15, 22, 30],
          backgroundColor: due_gradient,
        },
      ],
    };
  };

  const BandWidthOptions = {
    legend: {
      display: false,
    },
    scales: {
      yAxes: [
        {
          ticks: {
            beginAtZero: true,
          },
        },
      ],
    },
  };

  /****************bandwidth chart end**********************/

  /****************storage chart start**********************/

  const StorageData = (canvas) => {
    const ctx = canvas.getContext("2d");

    /*** Gradient ***/

    const invoice_send_gradient = ctx.createLinearGradient(0, 0, 0, 300);
    invoice_send_gradient.addColorStop(0, "#fe3eff");
    invoice_send_gradient.addColorStop(1, "#00f0ff");

    const payment_received_gradient = ctx.createLinearGradient(0, 0, 0, 300);
    payment_received_gradient.addColorStop(0, "#ff2260");
    payment_received_gradient.addColorStop(1, "#ff736b");

    const due_gradient = ctx.createLinearGradient(0, 0, 0, 300);
    due_gradient.addColorStop(0, "#423eff");
    due_gradient.addColorStop(1, "#fe3eff");
    /***************/

    return {
      labels: ["Video", "Audio", "Documents", "Free"],
      datasets: [
        {
          data: [15, 12, 16, 5],
          backgroundColor: [
            invoice_send_gradient,
            payment_received_gradient,
            due_gradient,
            "#BCBEC0",
          ],
        },
      ],
    };
  };

  const StorageOptions = {
    legend: {
      display: false,
    },
    maintainAspectRatio: false,
    responsive: true,
    cutoutPercentage: 50,
  };
  /****************storage chart end**********************/

  // new chart start
  const data = [
    { name: "", uv: 2000, pv: 2400, amt: 2400 },
    { name: "", uv: 6000, pv: 1398, amt: 2210 },
    { name: "", uv: 2000, pv: 9800, amt: 2290 },
    { name: "", uv: 4000, pv: 9800, amt: 2290 },
  ];

  return (
    <React.Fragment>
      <div className="wrapper">
        {/* PAGE CONTENT START */}
        <div className="content-wrapper pt-3">
          <div className="content">
            <div className="container-fluid layout-width">
              {/* Content Start */}
              <div className="row mt-2">
                {/* New chart  */}
                <div className="col-12">
                  {/* first row start */}
                  <div className="row mt-2 mb-4">
                    {/*institute div start */}
                    <div className="col-9">
                      <div className="row">
                        <div className="col-6">
                          <div className="info-box">
                            <div className="info-box-content attendance-thumbnail">
                              <img
                                src="img/attendance.png"
                                className="attendance-img mr-5"
                              />
                              <span className="info-box-text attendance-text">
                                Institute
                              </span>
                              {/* Institute chart start */}
                              <div className="mt-3">
                                <HorizontalBar
                                  data={instituteData}
                                  options={instituteOptions}
                                />
                              </div>
                              {/* Institute chart end */}
                            </div>
                            {/* /.info-box-content */}
                          </div>
                          {/* /.info-box */}
                        </div>
                        <div className="col-6">
                          <div className="info-box">
                            <div className="info-box-content attendance-thumbnail">
                              <img
                                src="img/attendance.png"
                                className="attendance-img mr-5"
                              />
                              <span className="info-box-text attendance-text">
                                Payment
                              </span>
                              {/* Payment chart start */}
                              <div className="mt-3">
                                <HorizontalBar
                                  data={paymentData}
                                  options={paymentOptions}
                                />
                              </div>

                              {/* Payment chart end */}
                            </div>
                            {/* /.info-box-content */}
                          </div>
                          {/* /.info-box */}
                        </div>
                      </div>
                    </div>
                    {/* institute full div end */}
                    <div className="col-3">
                      <div className="row">
                        {/* System Info */}
                        <div className="col-12">
                          <div className="info-box">
                            <div className="info-box-content attendance-thumbnail">
                              <img
                                src="img/attendance.png"
                                className="attendance-img mr-5"
                              />
                              <span className="info-box-text attendance-text mb-4">
                                System Info
                              </span>
                            </div>
                            {/* /.info-box-content */}
                          </div>
                          {/* /.info-box */}
                        </div>
                        {/* Storage chart start */}
                        <div className="col-12">
                          <div className="info-box">
                            <div className="info-box-content attendance-thumbnail">
                              <span
                                className="info-box-text attendance-text"
                                style={{ textAlign: "left", paddingLeft: "0" }}
                              >
                                Storage
                              </span>
                              <div className="mb-4 mt-1">
                                <Doughnut
                                  data={StorageData}
                                  width={100}
                                  height={80}
                                  options={StorageOptions}
                                />
                              </div>
                            </div>
                            {/* /.info-box-content */}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>

                  {/* second row start*/}
                  <div className="row mb-2">
                    <div className="col-9">
                      <div className="row">
                        {/* Live class chart start */}
                        <div className="col-8">
                          <div className="info-box">
                            <div className="info-box-content attendance-thumbnail">
                              <img
                                src="img/liveClass.png"
                                className="attendance-img mr-5"
                              />
                              <span className="info-box-text attendance-text">
                                Live Class
                              </span>
                              {/* Live class chart start */}
                              <div className="d-flex row mt-3">
                                <img
                                  src="img/LiveClass_1.png"
                                  className="img-fluid ml-2 mr-4"
                                  width="100"
                                />
                                <span
                                  className="current-number"
                                  style={{
                                    position: "absolute",
                                    top: "130px",
                                    left: "55px",
                                    fontSize: "24px",
                                    color: "#3abfb0",
                                  }}
                                >
                                  7
                                </span>
                                <img
                                  src="img/LiveClass_2.png"
                                  className="img-fluid mr-4"
                                  width="100"
                                />
                                <span
                                  className="current-number"
                                  style={{
                                    position: "absolute",
                                    top: "130px",
                                    left: "178px",
                                    fontSize: "24px",
                                    color: "#bf4d6c",
                                  }}
                                >
                                  56
                                </span>
                                <img
                                  src="img/LiveClass_3.png"
                                  className="img-fluid mr-4"
                                  width="100"
                                />
                                <span
                                  className="current-number"
                                  style={{
                                    position: "absolute",
                                    top: "130px",
                                    right: "209px",
                                    fontSize: "24px",
                                    color: "#00a8e2",
                                  }}
                                >
                                  100
                                </span>
                                <img
                                  src="img/LiveClass_4.png"
                                  className="img-fluid mr-2"
                                  width="100"
                                />
                                <span
                                  className="current-number"
                                  style={{
                                    position: "absolute",
                                    top: "130px",
                                    right: "81px",
                                    fontSize: "24px",
                                    color: "#7F3F98",
                                  }}
                                >
                                  220
                                </span>
                              </div>

                              {/* Live class chart end */}
                            </div>
                            {/* /.info-box-content */}
                          </div>
                          {/* /.info-box */}
                        </div>
                        {/* New User chart start */}
                        <div className="col-4">
                          <div className="info-box">
                            <div className="info-box-content attendance-thumbnail">
                              <img
                                src="img/calender.png"
                                className="attendance-img mr-5"
                              />
                              <span className="info-box-text attendance-text">
                                New User
                              </span>
                              {/* New User chart start */}
                              <div className="d-flex row mt-3 mb-2">
                                <img
                                  src="img/Asset11.png"
                                  className="img-fluid ml-2 mr-4"
                                  width="100"
                                  style={{
                                    backgroundColor: "#EBEEF3",
                                    padding: "16px 16px 48px 16px",
                                  }}
                                />
                                <span
                                  className="current"
                                  style={{
                                    position: "absolute",
                                    top: "118px",
                                    left: "44px",
                                    fontSize: "11px",
                                    color: "#808285",
                                  }}
                                >
                                  Current
                                </span>
                                <span
                                  className="current-number"
                                  style={{
                                    position: "absolute",
                                    top: "148px",
                                    left: "42px",
                                    fontSize: "24px",
                                    color: "#808285",
                                  }}
                                >
                                  450
                                </span>
                                <img
                                  src="img/Asset12.png"
                                  className="img-fluid mr-2"
                                  width="100"
                                  style={{
                                    backgroundColor: "#EBEEF3",
                                    padding: "16px 16px 48px 16px",
                                  }}
                                />
                                <span
                                  className="this-week"
                                  style={{
                                    position: "absolute",
                                    top: "118px",
                                    left: "166px",
                                    fontSize: "10px",
                                    color: "#808285",
                                  }}
                                >
                                  This Week
                                </span>
                                <span
                                  className="this-week-number"
                                  style={{
                                    position: "absolute",
                                    top: "148px",
                                    left: "166px",
                                    fontSize: "24px",
                                    color: "#808285",
                                  }}
                                >
                                  2100
                                </span>
                              </div>
                              {/* New User chart end */}
                            </div>
                            {/* /.info-box-content */}
                          </div>
                          {/* /.info-box */}
                        </div>
                      </div>
                    </div>

                    <div className="col-3">
                      <div className="row">
                        {/* bandwidth chart start */}
                        <div className="col-12">
                          <div className="info-box">
                            <div className="info-box-content attendance-thumbnail">
                              <span
                                className="info-box-text attendance-text"
                                style={{ textAlign: "left", paddingLeft: "0" }}
                              >
                                Bandwidth usages
                              </span>
                              <div className="mb-3 mt-5">
                                <Bar
                                  data={BandWidthData}
                                  options={BandWidthOptions}
                                  height={50}
                                  width={100}
                                />
                              </div>
                            </div>
                            {/* /.info-box-content */}
                          </div>
                          {/* /.info-box */}
                        </div>
                        {/* ram graph */}
                        <div className="col-12">
                          <div className="info-box">
                            <div className="info-box-content attendance-thumbnail">
                              <span
                                className="info-box-text attendance-text"
                                style={{ textAlign: "left", paddingLeft: "0" }}
                              >
                                Ram usages
                              </span>
                              <div className="mb-3 mt-5">
                                <AreaChart
                                  width={250}
                                  height={100}
                                  data={data}
                                  margin={{
                                    top: 10,
                                    right: 30,
                                    left: 0,
                                    bottom: 0,
                                  }}
                                >
                                  <CartesianGrid strokeDasharray="3 3" />
                                  <XAxis dataKey="name" />
                                  <YAxis />
                                  <Tooltip />
                                  <Area
                                    type="monotone"
                                    dataKey="uv"
                                    stroke="#465FFF"
                                    fill="#2F8FFF"
                                  />
                                </AreaChart>
                              </div>
                            </div>
                            {/* /.info-box-content */}
                          </div>
                          {/* /.info-box */}
                        </div>
                        {/* processor graph */}
                        <div className="col-12">
                          <div className="info-box">
                            <div className="info-box-content attendance-thumbnail">
                              <span
                                className="info-box-text attendance-text"
                                style={{ textAlign: "left", paddingLeft: "0" }}
                              >
                                Processor usages
                              </span>
                              <div className="mb-3 mt-5">
                                <AreaChart
                                  width={250}
                                  height={100}
                                  data={data}
                                  margin={{
                                    top: 10,
                                    right: 30,
                                    left: 0,
                                    bottom: 0,
                                  }}
                                >
                                  <CartesianGrid strokeDasharray="3 3" />
                                  <XAxis dataKey="name" />
                                  <YAxis />
                                  <Tooltip />
                                  <Area
                                    type="monotone"
                                    dataKey="uv"
                                    stroke="#465FFF"
                                    fill="#FF2260"
                                  />
                                </AreaChart>
                              </div>
                            </div>
                            {/* /.info-box-content */}
                          </div>
                          {/* /.info-box */}
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              {/* Content End */}
            </div>
          </div>
        </div>
        {/* PAGE CONTENT END */}
      </div>
    </React.Fragment>
  );
}
