import { Link } from "react-router-dom";
import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { useForm } from "react-hook-form";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import styled from "styled-components";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
import DatePicker from "react-datepicker";
const ErrorMsg = styled.p`
  color: red;
  font-size: 14px;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: bold;
`;

export default function InstituteCreate() {
  const { register, handleSubmit, errors } = useForm();

  //GET ALL PACKAGE NAME -START
  const [allPkgData, setAllPkgData] = useState([]);
  const [instituteTypes, setInstituteTypes] = useState([]);
  const [typeOfInst, setTypeOfInst] = useState("");
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "get",
        url: "/all-packages",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
      };

      const allPkgApiResp = await trackPromise(axios(config));
      // console.log("allPkgApiResp - " + allPkgApiResp.data.msg);
      setAllPkgData(allPkgApiResp.data.msg);
      //get institute type data
      var config = {
        method: "get",
        url: "/GetSettingsInstituteList",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
      };
      const response = await trackPromise(axios(config));
      //console.log("response - " + JSON.stringify(response.data.data));
      setInstituteTypes(response.data.data);
    }
    fetchData();
  }, []);
  //GET ALL PACKAGE NAME -END
  let parameter = [];
  let packages = {};
  function getPackageParameter(data) {
    //console.log("DATA: " + JSON.stringify(data));
    var total_parameter = 0;
    for (let key in data) {
      if (data.hasOwnProperty(key)) {
        if (key.includes("name-")) {
          total_parameter = total_parameter + 1;
        }
      }
    }

    for (let i = 0; i < total_parameter; i++) {
      let object = {};
      let name_key = `name-${i}`;
      let quantity_key = `quantity-${i}`;
      let price_key = `price-${i}`;

      object["name"] = data[name_key];
      object["quantity"] = data[quantity_key];
      object["price"] = data[price_key];

      parameter.push(object);
    }
    //console.log(JSON.stringify(parameter));

    return parameter;
  }
  let history = useHistory();
  // POST INSTITUTE & PACKAGE DATA -START
  const onSubmit = (data) => {
    if (subscription_s_date) {
      if (subscription_e_date) {
        if (last_payment_date) {
          // IF PACKAGE IS SELECTED FROM DROPDOWN THEN isPackageName = TRUE
          if (isPackageName) {
            // console.log(
            //   "Drop Down Package data: " +
            //     JSON.stringify(data) +
            //     " selected: " +
            //     selectedPackageId
            // );
            let institute_id =
              data.typeOfInst +
              "-" +
              data.instNameShortForm +
              "-" +
              data.serialNum;
            const instituteData = {
              package_id: selectedPackageId,
              institute_id: institute_id,
              password: data.password,
              name: data.instituteName,
              address: data.address,
              email: data.email,
              phone: data.phone,
              subscription_s_date: subscription_s_date,
              subscription_e_date: subscription_e_date,
              last_payment: last_payment_date,
              payment_amount: data.payableAmount,
            };
            console.log("Institute Data: " + JSON.stringify(instituteData));
            postData(instituteData);
            async function postData(instituteData) {
              let accessToken = localStorage.getItem("token");
              //alert(accessToken);
              accessToken = accessToken.substring(1, accessToken.length - 1);

              var config = {
                method: "post",
                url: "/institute-create",
                headers: {
                  Authorization: `Bearer ${accessToken}`,
                  "Content-Type": "application/json",
                },
                data: instituteData,
              };
              const createInstApiResp = await trackPromise(axios(config));
              //console.log("createInstApiResp - " + createInstApiResp.data);
              if (createInstApiResp.data.status === "ok") {
                // toast("Institute Created Successfully", {
                //   className: "toast-error",
                // });
                // send nisa apu data
                let integer_id = createInstApiResp.data.integer_id;
                console.log("integer_id: " + integer_id);
                const instituteData = {
                  subscription_s_date: subscription_s_date,
                  subscription_e_date: subscription_e_date,
                  last_payment: last_payment_date,
                  payment_amount: data.payableAmount,
                  firstName: data.instituteName,
                  email: data.email,
                  mobile: data.phone,
                  username: institute_id,
                  password: data.password,
                  instituteId: integer_id,
                };
                console.log("instituteData: " + JSON.stringify(instituteData));

                const sendApiResponse = await trackPromise(
                  axios.post(
                    "http://34.66.76.39:9091/api/other/user",
                    instituteData
                  )
                );
                if (sendApiResponse.data.status === "ok") {
                  toast("Institute Created Successfully", {
                    className: "toast-error",
                  });
                  history.push("/institute-list-view");
                } else {
                  toast(createInstApiResp.data.msg, {
                    className: "toast-error",
                  });
                }
                // send nisa apu data

                // history.push("/institute-list-view");
              } else {
                toast(createInstApiResp.data.msg, {
                  className: "toast-error",
                });
              }
            }
          } else {
            //console.log("DATA: " + JSON.stringify(data));

            packages = {
              title: data.packageTitle,
              description: data.packageDescription,
              display: data.display,
              type: "custom",
              payable_amount: data.payableAmount,
            };

            const parameters = getPackageParameter(data);
            //console.log("parameters: " + JSON.stringify(parameters));

            const packageData = {
              package: packages,
              parameters: parameters,
            };

            postData(packageData);
            async function postData(packageData) {
              let accessToken = localStorage.getItem("token");
              //alert(accessToken);
              accessToken = accessToken.substring(1, accessToken.length - 1);
              var config = {
                method: "post",
                url: "/package-save",
                headers: {
                  Authorization: `Bearer ${accessToken}`,
                  "Content-Type": "application/json",
                },
                data: packageData,
              };
              // console.log("PACKAGE DATA---" + JSON.stringify(packageData));
              const savePkgApiResp = await trackPromise(axios(config));
              //console.log("savePkgApiResp - " + savePkgApiResp.data);
              if (savePkgApiResp.data.status === "ok") {
                let packageId = savePkgApiResp.data.msg.package_id;
                //console.log("packageId: " + packageId);
                let institute_id =
                  data.typeOfInst +
                  "-" +
                  data.instNameShortForm +
                  "-" +
                  data.serialNum;
                const instituteData = {
                  package_id: packageId,
                  institute_id: institute_id,
                  password: data.password,
                  name: data.instituteName,
                  address: data.address,
                  email: data.email,
                  phone: data.phone,
                  subscription_s_date: subscription_s_date,
                  subscription_e_date: subscription_e_date,
                  last_payment: last_payment_date,
                  payment_amount: data.payableAmount,
                };
                //console.log("Institute Data: " + JSON.stringify(instituteData));
                postData(instituteData);
                async function postData(instituteData) {
                  let accessToken = localStorage.getItem("token");
                  //alert(accessToken);
                  accessToken = accessToken.substring(
                    1,
                    accessToken.length - 1
                  );

                  var config = {
                    method: "post",
                    url: "/institute-create",
                    headers: {
                      Authorization: `Bearer ${accessToken}`,
                      "Content-Type": "application/json",
                    },
                    data: instituteData,
                  };
                  const createInstApiResp = await trackPromise(axios(config));
                  //console.log("createInstApiResp - " + createInstApiResp.data);
                  if (createInstApiResp.data.status === "ok") {
                    // send nisa apu data
                    let integer_id = createInstApiResp.data.integer_id;
                    console.log("integer_id: " + integer_id);
                    const instituteData = {
                      subscription_s_date: subscription_s_date,
                      subscription_e_date: subscription_e_date,
                      last_payment: last_payment_date,
                      payment_amount: data.payableAmount,
                      firstName: data.instituteName,
                      email: data.email,
                      mobile: data.phone,
                      username: institute_id,
                      password: data.password,
                      instituteId: integer_id,
                    };
                    console.log(
                      "instituteData: " + JSON.stringify(instituteData)
                    );

                    const sendApiResponse = await trackPromise(
                      axios.post(
                        "http://34.66.76.39:9091/api/other/user",
                        instituteData
                      )
                    );
                    if (sendApiResponse.data.status === "ok") {
                      toast("Institute Created Successfully", {
                        className: "toast-error",
                      });
                      history.push("/institute-list-view");
                    } else {
                      toast(createInstApiResp.data.msg, {
                        className: "toast-error",
                      });
                    }
                    // send nisa apu data
                    // toast("Institute Created Successfully", {
                    //   className: "toast-error",
                    // });
                    // history.push("/institute-list-view");
                  } else {
                    toast(createInstApiResp.data.msg, {
                      className: "toast-error",
                    });
                  }
                }
              } else {
                toast(savePkgApiResp.data.msg, {
                  className: "toast-error",
                });
              }
            }
          }
        } else {
          setErrors_last_payment_date(true);
        }
      } else {
        setErrors_subscription_e_date(true);
      }
    } else {
      setErrors_subscription_s_date(true);
    }
  };
  // POST INSTITUTE & PACKAGE DATA -END
  // ---------------------------------------------------------------------------------------------
  /*PACKAGE START***************************************************************************************************/
  const [isCreateNewPackage, setIsCreateNewPackage] = useState(false);
  const [isPackageName, setIsPackageName] = useState(false);

  const [pkgTitle, setPkgTitle] = useState("");
  const [pkgDescription, setPkgDescription] = useState("");
  const [displayFrontEnd, setDisplayFrontEnd] = useState("On");
  const [payableAmount, setPayableAmount] = useState("");
  const [pkgParamList, setPkgParamList] = useState([]);
  const [selectedPackageId, setSelectedPackageId] = useState("");

  //select a package name and update package title and package description -start
  const handlePackageName = (e) => {
    if (!isPackageName) {
      setIsPackageName(true); //dropdown
    }
    var index = e.target.selectedIndex;
    var optionElement = e.target.childNodes[index];
    let pkgId = optionElement.getAttribute("data-id");
    setSelectedPackageId(pkgId);
    // console.log("PKGID : " + pkgId);
    // console.log("TITLE: " + allPkgData.find((x) => x.id === pkgId).title);
    // console.log(
    //   "DESCRIPTION: " + allPkgData.find((x) => x.id === pkgId).description
    // );
    setPkgTitle(allPkgData.find((x) => x.id === pkgId).title);
    setPkgDescription(allPkgData.find((x) => x.id === pkgId).description);
    setDisplayFrontEnd(allPkgData.find((x) => x.id === pkgId).display);
    setPayableAmount(allPkgData.find((x) => x.id === pkgId).payable_amount);
    // get package parameter (name, price, quantity) by pkgId
    async function fetchData(pkgId) {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "post",
        url: "/parameters",
        headers: {
          Authorization: `Bearer ${accessToken}`,
          "Content-Type": "application/json",
        },
        data: {
          package_id: pkgId,
        },
      };
      const pkgParamApiResp = await trackPromise(axios(config));
      // console.log(
      //   "pkgParamApiResp - " + JSON.stringify(pkgParamApiResp.data.msg)
      // );
      if (pkgParamApiResp.data.status === "ok") {
        setPkgParamList(pkgParamApiResp.data.msg);
        // toast("Institute Created Successfully", {
        //   className: "toast-error",
        // });
      } else {
        toast(pkgParamApiResp.data.msg, {
          className: "toast-error",
        });
      }
    }
    fetchData(pkgId);
  };
  //select a package name and update package title and package description -end

  /*PACKAGE END****************************************************************************************************/

  /*SHOW OR HIDE PASSWORD START*/
  const [passwordShown, setPasswordShown] = useState(false);
  const togglePasswordVisiblity = () => {
    setPasswordShown(passwordShown ? false : true);
  };
  /*SHOW OR HIDE PASSWORD END*/

  // modifing code start here-----------------------------------

  //GET PACKAGE PARAMETER -START
  const [data, setData] = useState([]);
  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "get",
        url: "/GetSettingsPackageList",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
      };

      let response = await trackPromise(axios(config));
      //console.log("response - " + JSON.stringify(response.data.data));
      let apiResponse = response.data.data;
      const apiResponseFiltered = apiResponse.filter(
        (res) => res.active === "1"
      );
      //console.log(apiResponseFiltered);
      setData(apiResponseFiltered);
    }
    fetchData();
  }, []);
  //GET PACKAGE PARAMETER -END

  // calculate payable amount
  function calculateTotalAmount() {
    let total_parameter = data.length;

    let total = 0.0;
    for (let i = 0; i < total_parameter; i++) {
      if (
        document.getElementById("name-" + i).value.toLowerCase() !== "discount"
      ) {
        if (document.getElementById("price-" + i).value) {
          //console.log("Price: " + document.getElementById("price-" + i).value);
          total =
            parseFloat(total) +
            parseFloat(document.getElementById("price-" + i).value);
          //console.log("Total: " + total);
        }
      }
    }

    return parseFloat(total);
  }
  const [discountError, setDiscountError] = useState(false);
  // calculate discount
  function calculateDiscount(totalAmount) {
    // -------------------------
    let total_parameter = data.length;
    for (let i = 0; i < total_parameter; i++) {
      if (
        document.getElementById("name-" + i).value.toLowerCase() === "discount"
      ) {
        if (document.getElementById("price-" + i).value) {
          let discountValue = parseFloat(
            document.getElementById("price-" + i).value
          );
          if (discountValue <= totalAmount) {
            setDiscountError(false);
            console.log("discountValue: " + discountValue + "i: " + i);
            totalAmount = totalAmount - discountValue;
          } else {
            setDiscountError(true);
          }
        }
      }
    }

    // ------------------------------

    return parseFloat(totalAmount);
  }

  // handle input change
  const handleInputChange = (e, index, package_amount, package_number) => {
    const { name, value } = e.target;
    // console.log(
    //   "Name: " +
    //     name +
    //     "\nValue: " +
    //     value +
    //     "\nIndex: " +
    //     index +
    //     "\npackage_amount: " +
    //     package_amount +
    //     "\npackage_number: " +
    //     package_number
    // );
    let quantity = parseInt(value);
    let price = parseFloat(package_amount);
    let number = parseInt(package_number);

    let unitPrice = parseFloat((price / number) * quantity);
    unitPrice = parseFloat(unitPrice.toFixed(2));
    document.getElementById("price-" + index).value = unitPrice;

    let totalAmount = calculateTotalAmount();
    //console.log("totalAmount: " + totalAmount);
    totalAmount = calculateDiscount(totalAmount);
    //console.log("After Discount: " + totalAmount);

    document.getElementById("payableAmount").value = totalAmount;
  };
  //date start-----------------
  const [subscription_s_date, setSubscription_s_date] = useState("");
  const [errors_subscription_s_date, setErrors_subscription_s_date] = useState(
    false
  );
  const [subscription_e_date, setSubscription_e_date] = useState("");
  const [errors_subscription_e_date, setErrors_subscription_e_date] = useState(
    false
  );
  const [last_payment_date, setLast_payment_date] = useState("");
  const [errors_last_payment_date, setErrors_last_payment_date] = useState(
    false
  );

  return (
    <React.Fragment>
      <div className="wrapper">
        {/* PAGE CONTENT START */}
        <div className="content-wrapper pt-3 pb-5">
          <div className="content">
            <div className="container-fluid layout-width">
              {/* Content Start */}
              <div className="row">
                <div className="col-12">
                  <div className="row">
                    <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                      <h4 className="heading">Create an Institute</h4>
                    </div>
                    <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                      <Link
                        to="/institute-list-view"
                        className="border ml-1 add-button shadow float-right"
                        title="Back"
                      >
                        <i className="fas fa-arrow-left add-button-icon"></i>
                      </Link>
                    </div>
                  </div>
                  <div className="card">
                    <div className="card-body p-0">
                      <div className="row" style={{ minHeight: "70vh" }}>
                        <div className="col-12" style={{ padding: "2rem" }}>
                          {/* ------------------------------------------------------------------ */}
                          {/* create package form start */}
                          <form
                            className="mt-4"
                            onSubmit={handleSubmit(onSubmit)}
                          >
                            {/* institute id */}
                            <div className="form-group row">
                              <div className="col-12">
                                <label>
                                  <b>Institute ID:</b>
                                </label>
                                <br />
                              </div>
                              <div className="col-4">
                                <label>
                                  <span className="red-star">*</span>Type of
                                  Institute
                                </label>
                                <select
                                  className="form-control"
                                  name="typeOfInst"
                                  defaultValue={typeOfInst}
                                  onChange={(e) =>
                                    setTypeOfInst(e.target.value)
                                  }
                                  ref={register({
                                    required: "* This Field is Required",
                                  })}
                                >
                                  <option defaultValue hidden>
                                    --Select--
                                  </option>
                                  {instituteTypes
                                    .sort((a, b) =>
                                      a.short_form.localeCompare(b.short_form)
                                    )
                                    .map((e, key) => {
                                      return e.active === "1" ? (
                                        <option
                                          key={key}
                                          data-id={e.id}
                                          value={e.short_form}
                                        >
                                          {e.short_form}
                                        </option>
                                      ) : null;
                                    })}
                                </select>
                                {errors.typeOfInst && (
                                  <ErrorMsg>
                                    {errors.typeOfInst.message}
                                  </ErrorMsg>
                                )}
                              </div>
                              <div className="col-4">
                                <label>
                                  <span className="red-star">*</span>Institute
                                  Name Short Form
                                </label>
                                <br />
                                <input
                                  type="text"
                                  className="form-control"
                                  name="instNameShortForm"
                                  ref={register({
                                    required: "* This Field is Required",
                                  })}
                                />
                                {errors.instNameShortForm && (
                                  <ErrorMsg>
                                    {errors.instNameShortForm.message}
                                  </ErrorMsg>
                                )}
                              </div>
                              <div className="col-4">
                                <label>
                                  <span className="red-star">*</span>Serial
                                  Number
                                </label>
                                <br />
                                <input
                                  type="number"
                                  className="form-control"
                                  name="serialNum"
                                  ref={register({
                                    required: "* This Field is Required",
                                  })}
                                />
                                {errors.serialNum && (
                                  <ErrorMsg>
                                    {errors.serialNum.message}
                                  </ErrorMsg>
                                )}
                              </div>
                            </div>
                            {/* password */}
                            <div className="form-group row">
                              <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                                <label>
                                  <span className="red-star">*</span>Password
                                </label>
                                <br />
                                <input
                                  id="password-field1"
                                  type={passwordShown ? "text" : "password"}
                                  className="form-control"
                                  name="password"
                                  ref={register({
                                    required: "* This Field is Required",
                                  })}
                                />

                                <span
                                  onClick={togglePasswordVisiblity}
                                  toggle="#password-field1"
                                  className={`fa fa-fw ${
                                    passwordShown ? "fa-eye-slash" : "fa-eye"
                                  } field-icon toggle-password`}
                                />
                                {errors.password && (
                                  <ErrorMsg>{errors.password.message}</ErrorMsg>
                                )}
                              </div>
                              <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                                <label>
                                  <span className="red-star">*</span>Institute
                                  Name
                                </label>
                                <br />
                                <input
                                  type="text"
                                  className="form-control"
                                  name="instituteName"
                                  ref={register({ required: true })}
                                />
                                {errors.instituteName && (
                                  <ErrorMsg>* This Field is Required</ErrorMsg>
                                )}
                              </div>
                            </div>
                            {/* Institute Address */}
                            <div className="form-group row">
                              <div className="col">
                                <label>
                                  <span className="red-star">*</span>Institute
                                  Address
                                </label>
                                <br />
                                <input
                                  type="text"
                                  className="form-control"
                                  name="address"
                                  ref={register({ required: true })}
                                />
                                {errors.address && (
                                  <ErrorMsg>* This Field is Required</ErrorMsg>
                                )}
                              </div>
                            </div>
                            {/* Email Address  and Phone / Mobile No.*/}
                            <div className="form-group row">
                              <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                                <label>
                                  <span className="red-star">*</span>Email
                                  Address
                                </label>
                                <br />
                                <input
                                  type="email"
                                  className="form-control"
                                  name="email"
                                  ref={register({ required: true })}
                                />
                                {errors.email && (
                                  <ErrorMsg>* This Field is Required</ErrorMsg>
                                )}
                              </div>
                              <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                                <label>
                                  <span className="red-star">*</span>Phone /
                                  Mobile No.
                                </label>
                                <br />
                                <input
                                  type="number"
                                  min="0"
                                  className="form-control"
                                  name="phone"
                                  ref={register({
                                    required: "* This Field is Required",
                                  })}
                                />
                                {errors.phone && (
                                  <ErrorMsg>{errors.phone.message}</ErrorMsg>
                                )}
                              </div>
                            </div>
                            {/* SELECT DROPDOWN Package Name */}
                            <div className="form-group row">
                              <div className="col">
                                <label>
                                  {isCreateNewPackage ? null : (
                                    <span className="red-star">*</span>
                                  )}
                                  Package Name
                                </label>
                                {/* Create New Package BUTTON */}
                                <button
                                  disabled={isPackageName ? true : false}
                                  onClick={() => setIsCreateNewPackage(true)}
                                  className="shadow float-right badge badge-pill badge-bg mb-3"
                                  style={{ padding: "10px 24px" }}
                                >
                                  Create New Package
                                </button>
                                <br />
                                {/* IF BUTTON PRESS THEN DROPDOWN WILL DISABLE */}
                                {isCreateNewPackage ? (
                                  <select
                                    className="form-control"
                                    name="package"
                                    disabled
                                    onChange={(e) => handlePackageName(e)}
                                    ref={register}
                                  >
                                    <option defaultValue hidden>
                                      --Select Package--
                                    </option>
                                  </select>
                                ) : (
                                  <select
                                    className="form-control"
                                    name="package"
                                    defaultValue="--Select Package--"
                                    onChange={(e) => handlePackageName(e)}
                                    ref={register}
                                  >
                                    <option defaultValue hidden>
                                      --Select Package--
                                    </option>
                                    {/* FOR SORTING ASCENDING ORDER ->  .sort((a, b) => a.name.localeCompare(b.name)) 
                                    ONLY SHOW DEFAULT PACKAGE*/}
                                    {allPkgData
                                      .sort((a, b) =>
                                        a.title.localeCompare(b.title)
                                      )
                                      .map((e, key) => {
                                        return e.type === "default" ? (
                                          <option
                                            key={key}
                                            data-id={e.id}
                                            value={e.title}
                                          >
                                            {e.title}
                                          </option>
                                        ) : null;
                                      })}
                                  </select>
                                )}
                                {errors.package && (
                                  <ErrorMsg>* This Field is Required</ErrorMsg>
                                )}
                              </div>
                            </div>
                            {/* IF BUTTON PRESS THEN DYNAMICALLY INPUT FIELD CREATED */}
                            {isCreateNewPackage ? (
                              <div>
                                {/* Package Name*/}
                                <div className="form-group row">
                                  <div className="col">
                                    <label>
                                      <span className="red-star">*</span>Package
                                      Name
                                    </label>
                                    <br />
                                    <input
                                      type="text"
                                      className="form-control"
                                      name="packageTitle"
                                      ref={register({ required: true })}
                                    />
                                    {errors.packageTitle && (
                                      <ErrorMsg>
                                        * This Field is Required
                                      </ErrorMsg>
                                    )}
                                  </div>
                                </div>
                                {/* Package Description */}
                                <div className="form-group row">
                                  <div className="col">
                                    <label>
                                      <span className="red-star">*</span>Package
                                      Description
                                    </label>
                                    <br />
                                    <textarea
                                      className="form-control"
                                      name="packageDescription"
                                      ref={register({ required: true })}
                                      rows="1"
                                      cols="30"
                                    ></textarea>
                                    {errors.packageDescription && (
                                      <ErrorMsg>
                                        * This Field is Required
                                      </ErrorMsg>
                                    )}
                                  </div>
                                </div>
                                {/* PACKAGE IS CREATED FROM BUTTON SO CUSTOM PACKAGE */}
                                <div className="form-group row">
                                  <div className="col">
                                    <input
                                      type="hidden"
                                      className="form-control"
                                      name="packageType"
                                      value="custom"
                                      ref={register}
                                    />
                                  </div>
                                </div>
                              </div>
                            ) : null}
                            {/* IF DROPDOWN SELECTED THEN INPUT WILL AUTO FILLUP AND VALUE WILL BE READONLY */}
                            {isPackageName ? (
                              <div>
                                {/* Package Title */}
                                <div className="form-group row">
                                  <div className="col">
                                    <label>
                                      <span className="red-star">*</span>Package
                                      Title
                                    </label>
                                    <br />
                                    <input
                                      type="text"
                                      readOnly
                                      value={pkgTitle}
                                      className="form-control"
                                      name="packageTitle"
                                      ref={register({ required: true })}
                                    />
                                    {errors.packageTitle && (
                                      <ErrorMsg>
                                        * This Field is Required
                                      </ErrorMsg>
                                    )}
                                  </div>
                                </div>
                                {/* Description */}
                                <div className="form-group row">
                                  <div className="col">
                                    <label>
                                      <span className="red-star">*</span>Package
                                      Description
                                    </label>
                                    <br />
                                    <textarea
                                      className="form-control"
                                      name="packageDescription"
                                      readOnly
                                      value={pkgDescription}
                                      ref={register({ required: true })}
                                      rows="1"
                                      cols="30"
                                    ></textarea>
                                    {errors.packageDescription && (
                                      <ErrorMsg>
                                        * This Field is Required
                                      </ErrorMsg>
                                    )}
                                  </div>
                                </div>
                                {/* PACKAGE IS CREATED FROM DROPDOWN SO DEFAULT PACKAGE */}
                                <div className="form-group row">
                                  <div className="col">
                                    <input
                                      type="hidden"
                                      className="form-control"
                                      name="packageType"
                                      value="default"
                                      ref={register}
                                    />
                                  </div>
                                </div>
                              </div>
                            ) : null}
                            {/* Package Display Front-end ? */}
                            <div className="form-group row">
                              <div className="col-4">
                                <label>
                                  <span className="red-star">*</span>
                                  Is Package Display Front-end ?
                                </label>
                                <br />
                                <div className="d-flex flex-sm-row flex-column">
                                  <div className="p-2">
                                    {displayFrontEnd === "On" ? (
                                      <input
                                        name="display"
                                        type="radio"
                                        className="mr-2"
                                        defaultValue="On"
                                        defaultChecked
                                        ref={register({ required: true })}
                                      />
                                    ) : (
                                      <input
                                        name="display"
                                        type="radio"
                                        className="mr-2"
                                        defaultValue="On"
                                        ref={register({ required: true })}
                                      />
                                    )}
                                    On
                                  </div>
                                  <div className="p-2">
                                    {displayFrontEnd === "Off" ? (
                                      <input
                                        name="display"
                                        className="mr-2"
                                        type="radio"
                                        defaultValue="Off"
                                        defaultChecked
                                        ref={register({ required: true })}
                                      />
                                    ) : (
                                      <input
                                        name="display"
                                        className="mr-2"
                                        type="radio"
                                        defaultValue="Off"
                                        ref={register({ required: true })}
                                      />
                                    )}
                                    Off
                                  </div>
                                </div>

                                {errors.display && (
                                  <ErrorMsg>* This Field is Required</ErrorMsg>
                                )}
                              </div>
                            </div>
                            {/* IF DROPDOWN SELECTED THEN INPUT WILL AUTO FILLUP AND VALUE WILL BE READONLY */}
                            {isPackageName ? (
                              <div className="form-group">
                                <label>
                                  <span className="red-star">*</span>Package
                                  Parameter
                                </label>
                                <br />
                                {pkgParamList.map((data, i) => {
                                  return (
                                    <div className="row" key={i}>
                                      <div className="col-sm-12 col-md-4 col-lg-4 col-xl-4">
                                        <label>Title</label>
                                        <br />
                                        <input
                                          type="text"
                                          readOnly
                                          className="form-control mb-3"
                                          id={`name-${i}`}
                                          name={`name-${i}`}
                                          defaultValue={data.name}
                                          ref={register({ required: true })}
                                        />
                                      </div>

                                      <div className="col-sm-12 col-md-3 col-lg-3 col-xl-3">
                                        <label>Quantity</label>
                                        <br />
                                        <input
                                          type="number"
                                          readOnly
                                          className="form-control mb-3"
                                          name={`quantity-${i}`}
                                          defaultValue={data.quantity}
                                          ref={register({ required: true })}
                                        />
                                        {errors.quantity && (
                                          <ErrorMsg>
                                            * This Field is Required
                                          </ErrorMsg>
                                        )}
                                      </div>

                                      <div className="col-sm-12 col-md-3 col-lg-3 col-xl-3">
                                        <label>Amount</label>
                                        <br />
                                        <input
                                          type="number"
                                          id={`price-${i}`}
                                          readOnly
                                          className="form-control mb-3"
                                          name={`price-${i}`}
                                          defaultValue={data.price}
                                          ref={register({ required: true })}
                                        />
                                      </div>
                                    </div>
                                  );
                                })}
                              </div>
                            ) : (
                              <div className="form-group">
                                <label>
                                  <span className="red-star">*</span>Package
                                  Parameter
                                </label>
                                <br />
                                {data.map((data, i) => {
                                  return (
                                    <div className="row" key={i}>
                                      <div className="col-sm-12 col-md-4 col-lg-4 col-xl-4">
                                        <label>Title</label>
                                        <br />
                                        <input
                                          type="text"
                                          readOnly
                                          className="form-control mb-3"
                                          id={`name-${i}`}
                                          name={`name-${i}`}
                                          defaultValue={data.package_item_name}
                                          ref={register({ required: true })}
                                        />
                                      </div>

                                      <div className="col-sm-12 col-md-3 col-lg-3 col-xl-3">
                                        <label>Quantity</label>
                                        <br />
                                        <input
                                          type="number"
                                          className="form-control mb-3"
                                          name={`quantity-${i}`}
                                          onChange={(e) =>
                                            handleInputChange(
                                              e,
                                              i,
                                              data.package_amount,
                                              data.package_number
                                            )
                                          }
                                          min="0"
                                          required
                                          ref={register({ required: true })}
                                        />
                                        {errors.quantity && (
                                          <ErrorMsg>
                                            * This Field is Required
                                          </ErrorMsg>
                                        )}
                                      </div>

                                      <div className="col-sm-12 col-md-3 col-lg-3 col-xl-3">
                                        <label>Amount</label>
                                        <br />
                                        <input
                                          type="number"
                                          id={`price-${i}`}
                                          readOnly
                                          className="form-control mb-3"
                                          name={`price-${i}`}
                                          ref={register({ required: true })}
                                        />
                                      </div>
                                    </div>
                                  );
                                })}
                                {discountError ? (
                                  <div className="col-12">
                                    <ErrorMsg>
                                      * Discount Value Should be Less Than
                                      Payable Amount
                                    </ErrorMsg>
                                  </div>
                                ) : null}
                              </div>
                            )}
                            {/* -------------------------------------------- */}

                            {/* Subscription Duration and Last Payment Date*/}
                            <div className="form-group row">
                              <div className="col-sm-12 col-md-12 col-lg-8 col-xl-6">
                                <label>
                                  <span className="red-star">*</span>
                                  Subscription Duration
                                </label>
                                <br />
                                <div className="d-flex flex-sm-row flex-column">
                                  <div>
                                    <DatePicker
                                      className="form-control"
                                      selected={subscription_s_date}
                                      //defaultValue={new Date()}
                                      onChange={(date) => {
                                        setSubscription_s_date(date);
                                        setErrors_subscription_s_date(false);
                                      }}
                                      dateFormat="dd/MM/yyyy"
                                      showYearDropdown
                                      scrollableMonthYearDropdown
                                      placeholderText="dd/mm/yyyy"
                                    />
                                    {errors_subscription_s_date ? (
                                      <ErrorMsg>
                                        * This Field is Required
                                      </ErrorMsg>
                                    ) : null}
                                  </div>
                                  <div>
                                    <p className="mt-2 mr-3 ml-3">to</p>
                                  </div>
                                  <div>
                                    <DatePicker
                                      className="form-control"
                                      selected={subscription_e_date}
                                      //defaultValue={new Date()}
                                      onChange={(date) => {
                                        setSubscription_e_date(date);
                                        setErrors_subscription_e_date(false);
                                      }}
                                      minDate={subscription_s_date}
                                      dateFormat="dd/MM/yyyy"
                                      showYearDropdown
                                      scrollableMonthYearDropdown
                                      placeholderText="dd/mm/yyyy"
                                    />
                                    {errors_subscription_e_date ? (
                                      <ErrorMsg>
                                        * This Field is Required
                                      </ErrorMsg>
                                    ) : null}
                                  </div>
                                </div>
                              </div>

                              <div className="col-sm-12 col-md-12 col-lg-4 col-xl-6">
                                <label>
                                  <span className="red-star">*</span>Last
                                  Payment Date
                                </label>
                                <br />
                                <DatePicker
                                  className="form-control"
                                  selected={last_payment_date}
                                  //defaultValue={new Date()}
                                  onChange={(date) => {
                                    setLast_payment_date(date);
                                    setErrors_last_payment_date(false);
                                  }}
                                  dateFormat="dd/MM/yyyy"
                                  showYearDropdown
                                  scrollableMonthYearDropdown
                                  placeholderText="dd/mm/yyyy"
                                />
                                {errors_last_payment_date ? (
                                  <ErrorMsg>* This Field is Required</ErrorMsg>
                                ) : null}
                              </div>
                            </div>
                            {/* payable amount */}
                            <div className="form-group row">
                              <div className="col-sm-12 col-md-4 col-lg-4 col-xl-4">
                                <label>Payable Amount</label>
                                <br />
                                <input
                                  type="number"
                                  readOnly
                                  id="payableAmount"
                                  name="payableAmount"
                                  defaultValue={
                                    payableAmount ? payableAmount : ""
                                  }
                                  className="form-control"
                                  ref={register({ required: true })}
                                />
                              </div>
                            </div>

                            <button
                              type="submit"
                              className="btn btn-update mt-2 mb-2"
                            >
                              Create
                            </button>
                          </form>
                          {/* create institute form end */}
                          <LoadingIndicator />
                          {/* ----------------------------------------------------------------------- */}
                        </div>
                      </div>
                    </div>
                    {/* /.card */}
                  </div>
                  {/* Profile Edit end */}
                </div>
              </div>
              {/* Content End */}
            </div>
          </div>
        </div>
        {/* PAGE CONTENT END */}
      </div>
    </React.Fragment>
  );
}
