import React, { useState } from "react";
import { Link, useHistory } from "react-router-dom";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import { toast } from "react-toastify";

export default function Datatable({ data }) {
  const history = useHistory();

  async function deletePackage(packageId, packageType) {
    if (packageType === "custom") {
      toast("Custom Package Can Not Deleted", {
        className: "toast-error",
      });
    } else {
      // console.log("package id: " + packageId);
      let accessToken = localStorage.getItem("token");
      //alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "post",
        url: `/package-delete`,
        headers: {
          Authorization: `Bearer ${accessToken}`,
          "Content-Type": "application/json",
        },
        data: {
          package_id: packageId,
        },
      };
      const deletePkgApiResp = await trackPromise(axios(config));
      //console.log("deletePkgApiResp - " + deletePkgApiResp.data);
      if (deletePkgApiResp.data.status === "ok") {
        toast("Package Deleted Successfully", {
          className: "toast-error",
        });
        history.go(0);
      } else {
        toast(deletePkgApiResp.data.msg, {
          className: "toast-error",
        });
      }
    }
  }
  return (
    <div className="table-responsive">
      <table className="table table-hover institute-table">
        <thead>
          <tr className="inst-list-heading">
            <th className="inst-list-th text-center">SL No.</th>
            <th className="inst-list-th text-center">Package Name</th>
            <th className="inst-list-th text-center">Package Type</th>
            <th className="inst-list-th text-center">Display</th>
            <th className="inst-list-th text-center" colSpan="3">
              Action
            </th>
          </tr>
        </thead>
        <tbody>
          {data.map((row, index) => (
            <tr key={index}>
              <td className="inst-list-td text-center">{index + 1}</td>
              <td className="inst-list-td text-center"> {row["title"]}</td>
              <td className="inst-list-td text-center">
                {row["type"].charAt(0).toUpperCase() + row["type"].slice(1)}
              </td>
              <td className="inst-list-td text-center"> {row["display"]}</td>
              <td
                className="inst-list-td text-center action"
                title="View Details"
                onClick={() =>
                  history.push({
                    pathname: "/package-details",
                    state: { id: row["id"] },
                  })
                }
              >
                <i className="far fa-eye action-icon"></i>
              </td>
              <td
                className="inst-list-td text-center action"
                title="Edit"
                onClick={() =>
                  history.push({
                    pathname: "/package-edit",
                    state: { id: row["id"] },
                  })
                }
              >
                <i className="far fa-edit action-icon"></i>
              </td>
              <td
                className="inst-list-td text-center action"
                title="Delete"
                onClick={() => history.push(`/package-edit/${row["id"]}`)}
                onClick={() => {
                  deletePackage(row["id"], row["type"]);
                }}
              >
                <i className="far fa-trash-alt action-icon"></i>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
