import React from "react";
import { Link } from "react-router-dom";
import "./password.css";
import { useForm } from "react-hook-form";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import styled from "styled-components";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
import logo from "./Logo2.png";
import backgroundImage from "./bg.jpg";
const ErrorMsg = styled.p`
  color: red;
  font-size: 14px;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: bold;
`;

export default function ForgetPassword(props) {
  const { register, handleSubmit, errors } = useForm(); // initialise the hook
  const onSubmit = (data) => {
    // console.log(data);
    trackPromise(
      axios.post("/password-reset-request", data).then((response) => {
        // console.log(JSON.stringify(response.data.status));
        if (response.data.status === "ok") {
          toast(
            "Password reset link has been sent to your mail.  \nPlease check your mail.",
            { closeOnClick: true, autoClose: false, className: "toast-error" }
          );
          props.history.push("/");
        } else {
          toast(response.data.msg, {
            className: "toast-error",
          });
        }
      })
    );
  };

  return (
    <>
      <div className="row" style={{ minHeight: "100vh", height: "100vh" }}>
        <div className="col-3 mt-5">
          {/* logo */}
          <div className="text-center mt-5">
            <img
              src={logo}
              alt="Babylon Logo"
              className="logo-babylon-login img-fluid"
              height="50"
              width="150"
            />
          </div>
          <div className="d-flex flex-column mt-2 mr-4 ml-4">
            <h4 className="font-weight-normal float-left">
              Forgot your password?
            </h4>
            <p className="font-weight-normal text-size float-left">
              Enter your email below to receive your password reset instruction
            </p>
          </div>
          <div className="mr-4 ml-4">
            <form className="mt-2" onSubmit={handleSubmit(onSubmit)}>
              <div className="mb-2">
                <input
                  type="email"
                  className="form-control"
                  name="email"
                  placeholder="Enter your email ID"
                  ref={register({ required: true })}
                />
                {errors.email && <ErrorMsg>* This Field is Required</ErrorMsg>}
              </div>

              <div className="input-group">
                <button
                  className="btn bg-color shadow text-white float-left mt-3 pr-5 pl-5"
                  type="submit"
                >
                  Send
                </button>
              </div>
              <p className="mb-5 mt-1 ">
                <Link to="/" className="small">
                  <i className="fas fa-long-arrow-alt-left mr-2"></i>Back to
                  Login Page
                </Link>
              </p>
            </form>
          </div>
        </div>

        {/* right side background image */}
        <div className="col-9">
          <div
            className="login-container"
            style={{
              backgroundImage: "url(" + backgroundImage + ")",
              backgroundRepeat: "no-repeat",
              backgroundSize: "100%",
              backgroundSize: "cover",
              position: "relative",
              width: "100%",
              minHeight: "100vh",
              height: "100vh",
            }}
          >
            <div className="site_title">
              <h1>Welcome to Learnerscafe LMS Software</h1>
              <p>
                Student:
                <br />
                Lorem Ipsum is simply dummy text of the printing and typesetting
                industry. Lorem Ipsum has been the industry's standard dummy
                text ever since the 1500s, when an unknown printer took a galley
                of type and scrambled it to make a type specimen book.
              </p>
              <p>
                Teacher:
                <br />
                Lorem Ipsum is simply dummy text of the printing and typesetting
                industry. Lorem Ipsum has been the industry's standard dummy
                text ever since the 1500s, when an unknown printer took a galley
                of type and scrambled it to make a type specimen book.
              </p>
              <p>
                Admin:
                <br />
                Lorem Ipsum is simply dummy text of the printing and typesetting
                industry. Lorem Ipsum has been the industry's standard dummy
                text ever since the 1500s, when an unknown printer took a galley
                of type and scrambled it to make a type specimen book.
              </p>
            </div>
            <div className="overlay"></div>
          </div>
        </div>
      </div>
      <LoadingIndicator />
    </>
  );
}
