import React, { useState, useEffect } from "react";
import { Link, useParams } from "react-router-dom";
import { useHistory } from "react-router-dom";
import { useLocation } from "react-router";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import { toast } from "react-toastify";
import DatatableParams from "./DatatableParams";
export default function PackageDetails() {
  const location = useLocation();
  //console.log(location);
  //GET ALL PACKAGE NAME -START
  const [packageDetail, setPackageDetail] = useState({});

  useEffect(() => {
    async function fetchData() {
      let accessToken = localStorage.getItem("token");
      // alert(accessToken);
      accessToken = accessToken.substring(1, accessToken.length - 1);
      var config = {
        method: "post",
        url: "/package-detail",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
        data: { package_id: location.state.id },
      };

      const pkgDetailApiResp = await trackPromise(axios(config));
      // console.log("pkgDetailApiResp - " + pkgDetailApiResp.data.msg);
      setPackageDetail(pkgDetailApiResp.data.msg);
    }
    fetchData();
  }, []);
  //GET ALL PACKAGE NAME -END
  //

  let history = useHistory();

  return (
    <React.Fragment>
      <div className="wrapper">
        {/* PAGE CONTENT START */}
        <div className="content-wrapper pt-3 pb-5">
          <div className="content">
            <div className="container-fluid layout-width">
              {/*  */}
              {/* Content Start */}
              <div className="row">
                <div className="col-12">
                  <div className="row">
                    <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                      <h4 className="heading">Package Details</h4>
                    </div>
                    <div className="col-sm-12 col-md-6 col-lg-6 col-xl-6">
                      <Link
                        to="/package-list-view"
                        className="border ml-1 add-button shadow float-right"
                        title="Back"
                      >
                        <i className="fas fa-arrow-left add-button-icon"></i>
                      </Link>
                    </div>
                  </div>
                  <div className="card">
                    {/* /.card-header */}
                    <div className="card-body">
                      <div className="tab-content">
                        <div className="active tab-pane" id="activity">
                          {/* Post */}
                          <div className="post">
                            <table className="table table-borderless table-responsive table-font-100">
                              <tbody>
                                <tr>
                                  <td className="pt-0 pb-0 text-left">Title</td>
                                  <td className="pt-0 pb-0 pl-0 text-left">
                                    : {packageDetail.title}
                                  </td>
                                </tr>
                                <tr>
                                  <td className="pt-0 pb-0 text-left">
                                    Description
                                  </td>
                                  <td className="pt-0 pb-0 pl-0 text-left">
                                    : {packageDetail.description}
                                  </td>
                                </tr>
                                <tr>
                                  <td className="pt-0 pb-0 text-left">
                                    Display
                                  </td>
                                  <td className="pt-0 pb-0 pl-0 text-left">
                                    : {packageDetail.display}
                                  </td>
                                </tr>
                                <tr>
                                  <td className="pt-0 pb-0 text-left">
                                    Package Type
                                  </td>
                                  <td className="pt-0 pb-0 pl-0 text-left">
                                    : {packageDetail.type}
                                  </td>
                                </tr>
                              </tbody>
                            </table>
                            {packageDetail.params ? (
                              <DatatableParams
                                data={packageDetail.params}
                                packageDetail={packageDetail}
                              />
                            ) : null}
                          </div>
                          <LoadingIndicator />
                        </div>
                      </div>
                      {/* /.tab-content */}
                    </div>
                    {/* /.card-body */}

                    {/* /.card */}
                  </div>
                  {/* Profile Edit end */}
                </div>
              </div>
              {/* Content End */}
            </div>
          </div>
        </div>
        {/* PAGE CONTENT END */}
      </div>
    </React.Fragment>
  );
}
