import "./login.css";
import React, { useState, useEffect, useRef } from "react";
import { useForm } from "react-hook-form";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import { Link } from "react-router-dom";
import styled from "styled-components";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import Footer from "../Layout/Footer";
import logo from "./Logo2.png";
import backgroundImage from "./bg.jpg";
const ErrorMsg = styled.p`
  color: red;
  font-size: 14px;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: bold;
`;

export default function Login(props) {
  const { register, handleSubmit, errors } = useForm(); // initialise the hook

  /*REMEMBER ME START*/
  const [isRememberMe, setIsRememberMe] = useState(false);
  /*REMEMBER ME END*/

  const onSubmit = (data) => {
    //console.log("Username & Password " + JSON.stringify(data));

    const email = data.email;

    trackPromise(
      axios.post("/authenticate", data).then((response) => {
        // console.log(JSON.stringify(response.data.status));
        if (response.data.status === "ok" || response.data.status === 200) {
          localStorage.setItem(
            "token",
            JSON.stringify(response.data.msg.token)
          );
          localStorage.setItem("role", JSON.stringify(response.data.msg.role));
          if (response.data.msg.role_details) {
            localStorage.setItem(
              "role_details",
              JSON.stringify(response.data.msg.role_details)
            );
          }
          localStorage.setItem("emailAddress", email);
          // console.log("email: " + email);
          // localStorage.setItem("email", "email");
          //console.log(this.props.history.push("/Dashboard"));
          if (isRememberMe) {
            localStorage.setItem("email", data.email);
            localStorage.setItem("password", data.password);
          } else {
            localStorage.removeItem("email");
            localStorage.removeItem("password");
          }
          props.history.push("/dashboard");
        } else {
          toast("Wrong Email Or Password", {
            className: "toast-error",
          });
        }
      })
    );
  };

  /*SHOW OR HIDE PASSWORD START*/
  const [passwordShown, setPasswordShown] = useState(false);
  const togglePasswordVisiblity = () => {
    setPasswordShown(passwordShown ? false : true);
  };
  /*SHOW OR HIDE PASSWORD END*/

  return (
    <>
      <div className="row" style={{ minHeight: "100vh", height: "100vh" }}>
        <div className="col-3 mt-5">
          {/* logo */}
          <div className="text-center mt-5">
            <img
              src={logo}
              alt="Babylon Logo"
              className="logo-babylon-login img-fluid"
              height="50"
              width="150"
            />
          </div>
          <div className="mr-4 ml-4">
            <form className="" onSubmit={handleSubmit(onSubmit)}>
              {/* user name */}
              <div className="mb-3">
                <label>User Name</label>
                <input
                  type="email"
                  className="form-control"
                  name="email"
                  defaultValue={
                    localStorage.getItem("email")
                      ? localStorage.getItem("email")
                      : ""
                  }
                  placeholder="Enter Your Email Address"
                  ref={register({ required: true })}
                />
                {errors.email && <ErrorMsg>* This Field is Required</ErrorMsg>}
              </div>
              {/* password */}
              <div className="mb-3">
                <label>Password</label>
                <input
                  id="password-field1"
                  type={passwordShown ? "text" : "password"}
                  className="form-control"
                  name="password"
                  defaultValue={
                    localStorage.getItem("password")
                      ? localStorage.getItem("password")
                      : ""
                  }
                  placeholder="Enter Your Password"
                  ref={register({ required: true })}
                />
                <span
                  onClick={togglePasswordVisiblity}
                  toggle="#password-field1"
                  className={`fa fa-fw ${
                    passwordShown ? "fa-eye-slash" : "fa-eye"
                  } field-icon toggle-password`}
                ></span>
                {errors.password && (
                  <ErrorMsg>* This Field is Required</ErrorMsg>
                )}
              </div>
              {/* remember me and forgot password */}
              <div className="form-check">
                <div className="float-left">
                  <input
                    type="checkbox"
                    className="form-check-input check-input"
                    id="exampleCheck1"
                    checked={isRememberMe}
                    onChange={() => setIsRememberMe(!isRememberMe)}
                  />
                  <label className="form-check-label" htmlFor="exampleCheck1">
                    Remember Me
                  </label>
                </div>
                <p className="mb-1">
                  <Link
                    to="/forget-password"
                    className="float-right small mt-1"
                  >
                    <label
                      className="form-check-label"
                      htmlFor="exampleCheck1"
                      style={{ fontSize: "16px" }}
                    >
                      Forgot Password?
                    </label>
                  </Link>
                </p>
              </div>

              <div className="input-group mb-5">
                <button
                  className="btn bg-color shadow text-white float-left mt-4 pr-5 pl-5"
                  type="submit"
                >
                  Login
                </button>
              </div>
            </form>
          </div>
          <div className="login-footer">
            <Footer />
          </div>
        </div>

        {/* right side background image */}
        <div className="col-9">
          <div
            className="login-container"
            style={{
              backgroundImage: "url(" + backgroundImage + ")",
              backgroundRepeat: "no-repeat",
              backgroundSize: "100%",
              backgroundSize: "cover",
              position: "relative",
              width: "100%",
              minHeight: "100vh",
              height: "100vh",
            }}
          >
            <div className="site_title">
              <h1>Welcome to Learnerscafe LMS Software</h1>
              <p>
                Student:
                <br />
                Lorem Ipsum is simply dummy text of the printing and typesetting
                industry. Lorem Ipsum has been the industry's standard dummy
                text ever since the 1500s, when an unknown printer took a galley
                of type and scrambled it to make a type specimen book.
              </p>
              <p>
                Teacher:
                <br />
                Lorem Ipsum is simply dummy text of the printing and typesetting
                industry. Lorem Ipsum has been the industry's standard dummy
                text ever since the 1500s, when an unknown printer took a galley
                of type and scrambled it to make a type specimen book.
              </p>
              <p>
                Admin:
                <br />
                Lorem Ipsum is simply dummy text of the printing and typesetting
                industry. Lorem Ipsum has been the industry's standard dummy
                text ever since the 1500s, when an unknown printer took a galley
                of type and scrambled it to make a type specimen book.
              </p>
            </div>
            <div className="overlay"></div>
          </div>
        </div>
      </div>
      <LoadingIndicator />
    </>
  );
}
