import React, { useState, useRef } from "react";
import { Link } from "react-router-dom";
import "./password.css";
import { useForm } from "react-hook-form";
import axios from "axios";
import { trackPromise } from "react-promise-tracker";
import { LoadingIndicator } from "../Loading/LoadingIndicator";
import styled from "styled-components";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "../../App.css";
import logo from "./Logo2.png";
import backgroundImage from "./bg2.jpg";
const ErrorMsg = styled.p`
  color: red;
  font-size: 14px;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: bold;
`;
export default function ResetPassword({ match, props }) {
  const { register, handleSubmit, errors, watch } = useForm(); // initialise the hook
  const password = useRef({});
  password.current = watch("password", "");
  const onSubmit = (data) => {
    // console.log(data);
    // data["property"] = value;
    const reset_token = match.params.token;
    data["token"] = reset_token;
    trackPromise(
      axios.post("/password-reset", data).then((response) => {
        // console.log(JSON.stringify(response.data.status));
        if (response.data.status === "ok") {
          toast("Password Reset Success", {
            className: "toast-error",
          });
          props.history.push("/");
        } else {
          toast(response.data.msg, {
            className: "toast-error",
          });
        }
      })
    );
  };
  /*SHOW OR HIDE PASSWORD START*/
  const [newPasswordShown, setNewPasswordShown] = useState(false);
  const [confirmPasswordShown, setConfirmPasswordShown] = useState(false);
  const toggleNewPasswordVisiblity = () => {
    setNewPasswordShown(newPasswordShown ? false : true);
  };
  const toggleConfirmPasswordVisiblity = () => {
    setConfirmPasswordShown(confirmPasswordShown ? false : true);
  };
  /*SHOW OR HIDE PASSWORD END*/
  return (
    <>
      <div className="row" style={{ minHeight: "100vh", height: "100vh" }}>
        <div className="col-3 mt-5">
          {/* logo */}
          <div className="text-center mt-5">
            <img
              src={logo}
              alt="Babylon Logo"
              className="logo-babylon-login img-fluid"
              height="50"
              width="150"
            />
          </div>
          <div className="d-flex flex-column mt-2 mr-4 ml-4">
            <h4 className="font-weight-normal">Reset Password</h4>
          </div>
          <div className="mr-4 ml-4">
            <form className="mt-3" onSubmit={handleSubmit(onSubmit)}>
              <div className="mb-3">
                <label>Email</label>
                <input
                  type="email"
                  className="form-control"
                  name="email"
                  placeholder="Enter Your Email Address"
                  ref={register({ required: true })}
                />
                {errors.email && <ErrorMsg>* This Field is Required</ErrorMsg>}
              </div>
              <div className="mb-3">
                <label>New Password</label>
                <input
                  id="password-field2"
                  type={newPasswordShown ? "text" : "password"}
                  className="form-control"
                  placeholder="Enter Your New Password"
                  name="password"
                  ref={register({
                    required: "* This Field is Required",
                    minLength: {
                      value: 8,
                      message: "* Minimum 8 Character Required",
                    },
                  })}
                />
                <span
                  onClick={toggleNewPasswordVisiblity}
                  toggle="#password-field2"
                  className={`fa fa-fw ${
                    newPasswordShown ? "fa-eye-slash" : "fa-eye"
                  } field-icon toggle-password`}
                />
                {errors.password && (
                  <ErrorMsg>{errors.password.message}</ErrorMsg>
                )}
              </div>
              <div className="mb-2">
                <label>Confirm Password</label>
                <input
                  id="password-field3"
                  type={confirmPasswordShown ? "text" : "password"}
                  className="form-control"
                  placeholder="Enter Your Confirm Password"
                  name="password_confirmation"
                  ref={register({
                    required: "* This Field is Required",
                    minLength: {
                      value: 8,
                      message: "* Minimum 8 Character Required",
                    },
                    validate: (value) =>
                      value === password.current ||
                      "Both Password Should Match",
                  })}
                />
                <span
                  onClick={toggleConfirmPasswordVisiblity}
                  toggle="#password-field3"
                  className={`fa fa-fw ${
                    confirmPasswordShown ? "fa-eye-slash" : "fa-eye"
                  } field-icon toggle-password`}
                />
                {errors.password_confirmation && (
                  <ErrorMsg>{errors.password_confirmation.message}</ErrorMsg>
                )}
              </div>

              <div className="input-group">
                <button
                  className="btn bg-color shadow text-white float-left mt-4 pr-4 pl-4"
                  type="submit"
                >
                  Reset Password
                </button>
              </div>
              <p className="mb-5 mt-1">
                <Link to="/" className="small">
                  <i className="fas fa-long-arrow-alt-left mr-2"></i>Back to
                  Login Page
                </Link>
              </p>
            </form>
          </div>
        </div>

        {/* right side background image */}
        <div className="col-9">
          <div
            className="login-container"
            style={{
              backgroundImage: "url(" + backgroundImage + ")",
              backgroundRepeat: "no-repeat",
              backgroundSize: "100%",
              backgroundSize: "cover",
              position: "relative",
              width: "100%",
              minHeight: "100vh",
              height: "100vh",
            }}
          >
            <div className="site_title">
              <h1>Welcome to Learnerscafe LMS Software</h1>
              <p>
                Student:
                <br />
                Lorem Ipsum is simply dummy text of the printing and typesetting
                industry. Lorem Ipsum has been the industry's standard dummy
                text ever since the 1500s, when an unknown printer took a galley
                of type and scrambled it to make a type specimen book.
              </p>
              <p>
                Teacher:
                <br />
                Lorem Ipsum is simply dummy text of the printing and typesetting
                industry. Lorem Ipsum has been the industry's standard dummy
                text ever since the 1500s, when an unknown printer took a galley
                of type and scrambled it to make a type specimen book.
              </p>
              <p>
                Admin:
                <br />
                Lorem Ipsum is simply dummy text of the printing and typesetting
                industry. Lorem Ipsum has been the industry's standard dummy
                text ever since the 1500s, when an unknown printer took a galley
                of type and scrambled it to make a type specimen book.
              </p>
            </div>
            <div className="overlay"></div>
          </div>
        </div>
      </div>
      <LoadingIndicator />
    </>
  );
}
